.OurServices {
    margin: 2rem auto; /* Centrage de la section */
    max-width: 90%; /* Évite un affichage trop large */
    color: black;
    text-align: center; /* Centre tout le contenu */
}

.OurServices h1 {
    font-size: 3rem;
    font-weight: bold; /* Met en gras */
}




.servicecard{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;

}
.s-card{
    width: 32%;
    
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem .8rem;

}

.s-image{
    height: 400px;
    overflow: hidden;
    border-radius: 7px;

}
.s-image img {
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}
.s-image:hover img{
    transform: scale(1.3);

}

.s-card h4 {
    font-size: 1.3rem;
    padding: .9rem 0 .2rem 0;
}
@media screen and (max-width:850px) {
    .OurServices{
        margin: 4rem 2rem;
    }
    .servicecard{
        flex-direction: column;
    
    }
    .s-card{
        width: 100%;
        margin-bottom: 1.5rem;
    }
    
}